import {connect} from 'react-redux'
import DataPage from 'Components/DataPage'

// redux action 
import {
    findTopClosestAddress,
    clearTopAddress,
    suggest3DateTime,
    setInitialBooking,
    findClosestSalesperson,
    loadGlobalSetting,
} from 'Actions'

// map state to props
const mapStateToProps = ({ authUser, allData }) => {
    const { user } = authUser
    const { top3address, loading, suggestDates, availableDateTime, findPerson, suggestedPersonArray, global } = allData
    return { user, top3address, loading, suggestDates, availableDateTime, findPerson, suggestedPersonArray, global }
}

const DataContainer = connect(mapStateToProps, {
    findTopClosestAddress,
    clearTopAddress,
    suggest3DateTime,
    setInitialBooking,
    findClosestSalesperson,
    loadGlobalSetting,
})(DataPage)

export default DataContainer
