/* eslint-disable react/no-array-index-key */
import React from 'react'
import moment from 'moment'

class Personlist extends React.Component {
    constructor() {
        super()
        this.state = {
            pageIndex: 0,
        }
    }

    handlerSelectPerson = (key) => {
        this.props.onSelectPerson(this.state.pageIndex*5 + key)
    }

    handlerLeft = () => {
        this.setState(prevState => {
            const currentIndex = prevState.pageIndex === 0 ? 0 : prevState.pageIndex - 1
            return {
                pageIndex: currentIndex,
            }

        })
    }

    handlerRight = () => {
        this.setState(prevState => {
            const currentIndex = prevState.pageIndex*5+5 > this.props.suggestedPersons.length-1 ? prevState.pageIndex : prevState.pageIndex + 1
            return {
                pageIndex: currentIndex,
            }

        })
    }

    render() {

        let personlist = ''
        const lastIndex = this.state.pageIndex*5+5 > this.props.suggestedPersons.length-1 ? this.props.suggestedPersons.length : this.state.pageIndex*5+5
        const currentPersons = this.props.suggestedPersons.slice(this.state.pageIndex*5, lastIndex)

        personlist = currentPersons.map((personItem, key) => {

            const currentDate = personItem.suggestDate
            return <div className="fraction js-day-wrapper available" key={ key } onClick={ e => this.handlerSelectPerson(key) }>
                <div className={ this.props.selectedPerson && (this.props.selectedPerson.sp_pk === personItem.sp_pk) ? 'selday personitem' : 'day personitem' } >
                    <div>
                        <strong className="shorthand">{personItem.name}</strong>
                    </div>
                    <div>
                        <div className="muted shorthand">{moment(currentDate).format('MMM DD, hh:mm')}</div>
                    </div>
                    <i className="icon-arrow-right"></i>
                </div>
            </div>
        })
    
        const leftDiabled = this.state.pageIndex === 0 ? true : false
        
        const rightDiabled = this.state.pageIndex*5+5 > this.props.suggestedPersons.length-1 ? true : false
        return (
            <div className="wrapper">

                <div className="js-days-region mvl">
                    <div className="week-view">
                        
                        <h2 className="mbl text-center">Suggested Sales Person</h2>
                        
                        <div className="js-days-region">
                            <div className="period row">
                                <div className="centered js-days-body">
                                    <div className={ leftDiabled ? 'angle-left disabled' : 'angle-left' } onClick={ e => this.handlerLeft() }>
                                        <i className="ti-angle-left"></i>
                                    </div>
                                    {personlist}
                                    <div className={ rightDiabled ? 'angle-right disabled' : 'angle-right' } onClick={ e => this.handlerRight() }>
                                        <i className="ti-angle-right"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Personlist
